<template>
  <div>
    <h3 class="page-head">
      <router-link to="/admin/setting">
        <i class="el-icon-arrow-left"></i>
      </router-link>/
    </h3>
    <div class="title"></div>
    <div class="content">
      <el-form ref="form" :model="formData" :rules="rules" label-width="120px">
        <el-form-item label="APPID" prop="app_id">
          <el-input v-model="formData.app_id"></el-input>
          <span>APPID ID</span>
        </el-form-item>
        <el-form-item label="SecretID" prop="secret_id">
          <el-input v-model="formData.secret_id"></el-input>
          <span>SecretID </span>
        </el-form-item>
        <el-form-item label="SecretKEY" prop="secret_key">
          <el-input v-model="formData.secret_key"></el-input>
          <span>SecretKEY </span>
        </el-form-item>
        <el-form-item label="Bucket" prop="bucket">
          <el-input v-model="formData.bucket"></el-input>
          <span>bucket</span>
        </el-form-item>
        <el-form-item label="" prop="region">
          <el-input v-model="formData.region"></el-input>
          <span>bucket: ap-shanghai</span>
        </el-form-item>
        <el-form-item label="Url" prop="cdn">
          <el-input v-model="formData.cdn"></el-input>
          <span>urlhttp://https:// /http://abc.com</span>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      formData: {
        key: 'cloud_cosv5',
        app_id: '',
        secret_id: '',
        secret_key: '',
        bucket: '',
        region: '',
        cdn: ''
      },
      rules: {
        app_id: [{ required: true, message: 'APPID', trigger: 'blur' }],
        secret_id: [{ required: true, message: 'SecretID', trigger: 'blur' }],
        secret_key: [{ required: true, message: 'SecretKEY', trigger: 'blur' }],
        bucket: [{ required: true, message: 'Bucket', trigger: 'blur' }],
        region: [{ required: true, message: 'Bucket', trigger: 'blur' }],
        cdn: [{ required: true, message: 'Url', trigger: 'blur' }]
      }
    }
  },
  created() {
    this.init()
  },
  methods: {
    init() {
      this.$post('/admin/setting/show', { key: 'cloud_cosv5' })
        .then(res => {
          this.formData.app_id = res.value.app_id
          this.formData.secret_id = res.value.secret_id
          this.formData.secret_key = res.value.secret_key
          this.formData.bucket = res.value.bucket
          this.formData.region = res.value.region
          this.formData.cdn = res.value.app_id
        })
        .catch(() => {})
    },
    onSubmit() {
      this.$refs['form'].validate(valid => {
        if (valid) {
          this.$post('/admin/setting/save', this.formData)
            .then(() => {
              this.$message('')
            })
            .catch(() => {
              this.$message('')
            })
        } else {
          return false
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.title {
  width: 100%;
  height: 30px;
  font-size: 14px;
  color: #3296fa;
  line-height: 30px;
  background-color: #f4f5f9;
  text-align: center;
}
.content {
  width: 800px;
  margin-top: 69px;
  input {
    border-radius: 2px;
    border: solid 1px #eeeeee;
  }
  /deep/ .el-input__inner {
    height: 35px;
  }
  button {
    width: 120px;
    height: 35px;
    padding: 9px 20px;
  }
}
</style>